import React, {useEffect} from 'react';
import CardContent from "@material-ui/core/CardContent";
import TableContainer from "@material-ui/core/TableContainer";
import Paper from "@material-ui/core/Paper";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Card from "@material-ui/core/Card";
import {makeStyles, withStyles} from "@material-ui/core/styles";
import TableCell from "@material-ui/core/TableCell";
import Button from "@material-ui/core/Button";
import TableBody from "@material-ui/core/TableBody";
import PropTypes from "prop-types";
import {useRootContext} from "../../libs/ContextLibs";
import Typography from "@material-ui/core/Typography";
import ReactToPrint from "react-to-print";
import {Constants} from "../../utils/Constants";


const useStyles = makeStyles({
  root: {
    minWidth: 275,
    marginLeft: 48,
    marginRight: 48,
    display: "block",
    clear: 'right',
  },
  tableCell: {
    borderRight: '1px solid lightgrey',
    paddingRight: 16,
  },
  headerText: {
    display: 'block',
    paddingBottom: 16
  },
  printButton: {
    float: 'right',
    marginLeft: 48,
    marginRight: 48,
    paddingBottom: 16,
    display: "block",
    margin: "auto",
    position: "relative"
  },
  tableContainer: {
    minHeight: 200,
    overflowX: 'hidden'
  },
  tableHeader: {
    backgroundColor: '#e0e0e0'
  }
});

const StyledTableCell = withStyles(() => ({
  head: {
    fontSize: 12,
    fontWeight: "bold"
  },
}))(TableCell);

export default function OrderInsight(props) {
  const classes = useStyles();
  const rootContext = useRootContext();
  const [rearrangedOrder, setRearrangedOrder] = React.useState({});
  const sum = 'sum';
  const reportRef = React.createRef();

  useEffect(() => {
    setRearrangedOrder(rearrangeOrderForReport(props.orderQueue));

    return () => {
      setRearrangedOrder({});
    };
  }, [props.orderQueue])


  const newRearrangedOrder = () => {
    const ret = {};
    ret[sum] = 0;
    rootContext.userInfo.branches.map((branch, bIndex) => {
      if (ret[branch] == null) ret[branch] = {};
      rootContext.userInfo.delivery_partners.map((dPartner, dIndex) => {
        ret[branch][dPartner] = 0;
      });
      ret[branch][sum] = 0;
    });
    return ret;
  }

  const rearrangeOrderForReport = (orderQueue) => {
    let rearrangedOrder = {};
    Object.keys(orderQueue).forEach(orderIdx => {
      const order = orderQueue[orderIdx];
      if(Constants.ORDER_PROCESS_STATUS.delivered.localeCompare(order.process_status) !== 0) {
        return;
      }
      order.menu_code_list.map((menu_code, idx) => {
        if(rearrangedOrder[menu_code] == null) {
          rearrangedOrder[menu_code] = newRearrangedOrder();
        }
        rearrangedOrder[menu_code][order.branch][order.delivery_partner] += order.quantity_list[idx];
        rearrangedOrder[menu_code][order.branch][sum] += order.quantity_list[idx];
        rearrangedOrder[menu_code][sum] += order.quantity_list[idx];
      });
    })
    console.log(rearrangedOrder);
    return rearrangedOrder;
  }

  const getMenuName = (menuCode) => {
    return rootContext.menuList.filter(menu => menu.menu_code === menuCode ? true : false)[0].name;
  }

  const getMenuRate = (menuCode) => {
    return rootContext.menuList.filter(menu => menu.menu_code === menuCode ? true : false)[0].price;
  }

  const getBranchAndDeliveryPartnerCount = () => {
    return rootContext.userInfo.branches.length + rootContext.userInfo.delivery_partners.length;
  }

  const getTableBody = () => {
    const tableCell = [];
    if(rootContext.userInfo.branches != null) {
      console.log('Rearranged Order: ', rearrangedOrder);
      Object.keys(rearrangedOrder).map((order, orderIdx) => {
        console.log('in map', rearrangedOrder[order]);
      });
      tableCell.push(
        Object.keys(rearrangedOrder).map((orderKey, orderIdx) => (
          rootContext.userInfo.branches.map((branch, bIndex) => (
            rootContext.userInfo.delivery_partners.map((dPartner, dIndex) => (
              <TableRow key={bIndex + dIndex}>
                {(bIndex === 0 && dIndex === 0) && <TableCell rowSpan={getBranchAndDeliveryPartnerCount()}
                                                              className={classes.tableCell} align={'center'}>{orderKey}</TableCell>}
                {(bIndex === 0 && dIndex === 0) && <TableCell rowSpan={getBranchAndDeliveryPartnerCount()}
                                                              className={classes.tableCell} align={'center'}>{getMenuName(orderKey)}</TableCell>}
                {(bIndex === 0 && dIndex === 0) && <TableCell rowSpan={getBranchAndDeliveryPartnerCount()}
                                                              className={classes.tableCell} align={'center'}>{getMenuRate(orderKey)}</TableCell>}
                {(dIndex === 0) && (<TableCell rowSpan={rootContext.userInfo.delivery_partners.length}
                                               className={classes.tableCell} align={"left"}>{branch}</TableCell>)}
                <TableCell className={classes.tableCell} align="center">{dPartner}</TableCell>
                <TableCell className={classes.tableCell}
                           align="center">{rearrangedOrder[orderKey][branch][dPartner]}</TableCell>
                {(dIndex === 0) && <TableCell rowSpan={rootContext.userInfo.delivery_partners.length} className={classes.tableCell}
                           align="center">{rearrangedOrder[orderKey][branch][sum]}</TableCell>}
                {(bIndex === 0 && dIndex === 0) && <TableCell rowSpan={getBranchAndDeliveryPartnerCount()} className={classes.tableCell}
                           align="center">{rearrangedOrder[orderKey][sum]}</TableCell>}
                {(bIndex === 0 && dIndex === 0) && <TableCell rowSpan={getBranchAndDeliveryPartnerCount()} className={classes.tableCell}
                           align="center">{getMenuRate(orderKey) * rearrangedOrder[orderKey][sum]}</TableCell>}
              </TableRow>
            ))
          ))
        ))
      );
    }
    const tableBody = [];
    tableBody.push(
      <TableBody>
        {tableCell}
      </TableBody>
    );

    return tableBody;
  }



  return (
    <div style={{display: "block"}}>
      <div className={classes.printButton}>
        <ReactToPrint content={() => reportRef.current} trigger={() =>
          <Button color="secondary" variant={"outlined"}>
            Print
          </Button>
        }
          pageStyle={"@page { size: landscape; }"}
        />
      </div>
      <Card className={classes.root} variant="outlined" ref={reportRef}>
        <CardContent>
          <div className={classes.headerText}>
            <Typography>
              <b>{rootContext.userInfo.name}</b>
              <span style={{fontSize: '.5rem'}}> powered by {process.env.REACT_APP_POWERED_BY}</span>
            </Typography>
            <Typography>
              <b>Report Generation TIme: </b> {Date()}
            </Typography>
            <Typography>
              <b>Type:</b> Daily Order Report
            </Typography>
          </div>
          <TableContainer className={classes.tableContainer} component={Paper}>
            <Table  size="small" aria-label="a dense table">
              <TableHead className={classes.tableHeader}>
                <TableRow>
                  <StyledTableCell>{"Menu Code"}</StyledTableCell>
                  <StyledTableCell align="center">{'Name'}</StyledTableCell>
                  <StyledTableCell align="center">{'Rate'}</StyledTableCell>
                  <StyledTableCell align="right">{'Branch'}</StyledTableCell>
                  <StyledTableCell align="center">{'Delivery Partner'}</StyledTableCell>
                  <StyledTableCell align="center">{'Quantity Served'}</StyledTableCell>
                  <StyledTableCell align="center">{'Quantity Branch'}</StyledTableCell>
                  <StyledTableCell align="center">{'Quantity Total'}</StyledTableCell>
                  <StyledTableCell align="center">{'Revenue'}</StyledTableCell>
                </TableRow>
              </TableHead>
              {getTableBody(props.order)}
            </Table>
          </TableContainer>
        </CardContent>
      </Card>
    </div>
  );
}


OrderInsight.propTypes = {
  orderQueue: PropTypes.any.isRequired,
}