import React, {useEffect} from 'react';
import CardContent from "@material-ui/core/CardContent";
import TableContainer from "@material-ui/core/TableContainer";
import Paper from "@material-ui/core/Paper";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Card from "@material-ui/core/Card";
import {makeStyles, withStyles} from "@material-ui/core/styles";
import TableCell from "@material-ui/core/TableCell";
import Button from "@material-ui/core/Button";
import TableBody from "@material-ui/core/TableBody";
import PropTypes from "prop-types";
import {useRootContext} from "../../libs/ContextLibs";
import {Constants} from "../../utils/Constants";
import Typography from "@material-ui/core/Typography";
import ReactToPrint from "react-to-print";


const useStyles = makeStyles({
  root: {
    minWidth: 275,
    marginLeft: 48,
    marginRight: 48,
    display: "block",
    clear: 'right',
  },
  tableCell: {
    borderRight: '1px solid lightgrey',
    paddingRight: 16,
  },
  headerText: {
    display: 'block',
    paddingBottom: 16
  },
  printButton: {
    float: 'right',
    marginLeft: 48,
    marginRight: 48,
    paddingBottom: 16,
    display: "block",
    margin: "auto",
    position: "relative"
  },
  tableContainer: {
    overflowX: 'hidden'
  },
  tableHeader: {
    backgroundColor: '#e0e0e0',
  }
});

const StyledTableCell = withStyles(() => ({
  head: {
    fontSize: 12,
    fontWeight: "bold"
  },
}))(TableCell);

export default function MiniReport(props) {
  const classes = useStyles();
  const rootContext = useRootContext();
  const [orders, setOrders] = React.useState([]);

  const reportRef = React.createRef();

  useEffect(() => {
    const newOrderList = [];
    Object.keys(props.orderQueue).forEach(order => {
      newOrderList.push(props.orderQueue[order]);
    });
    setOrders(newOrderList);


    return () => setOrders([]);
  }, [props.orderQueue])

  const getTotalNumberOfOrder = (branch, dPartner) => {
    console.log(branch, dPartner);
    let count = 0;
    orders.forEach(order => {
      if(Constants.ORDER_PROCESS_STATUS.cancelled.localeCompare(order.process_status) === 0) return;
      if(branch.localeCompare(order.branch) === 0 && dPartner.localeCompare(order.delivery_partner) === 0) count++;
    });
    console.log('Order count: ', count);
    return count;
  }

  const getTotalDiscountedAmount = (branch, dPartner) => {
    let discountedAmount = 0;
    orders.forEach(order => {
      if(Constants.ORDER_PROCESS_STATUS.cancelled.localeCompare(order.process_status) === 0) return;
      if(branch.localeCompare(order.branch) === 0 && dPartner.localeCompare(order.delivery_partner) === 0) {
        discountedAmount += order.discount;
      }
    });
    return discountedAmount;
  }

  const getTotalPrice = (branch) => {
    let amount = 0;
    orders.forEach(order => {
      if(Constants.ORDER_PROCESS_STATUS.cancelled.localeCompare(order.process_status) === 0) return;
      if(branch.localeCompare(order.branch) === 0) amount += order.total_price;
    });
    return amount;
  }

  const getTotalPriceByDeliveryPartner = (branch, dPartner) => {
    let amount = 0;
    orders.forEach(order => {
      if(Constants.ORDER_PROCESS_STATUS.cancelled.localeCompare(order.process_status) === 0) return;
      if(branch.localeCompare(order.branch) === 0 && dPartner.localeCompare(order.delivery_partner) === 0){
        amount += order.total_price;
      };
    });
    return amount;
  }

  const getTotalOrderCount = () => {
    let count = 0;
    orders.forEach(order => {
      if(Constants.ORDER_PROCESS_STATUS.cancelled.localeCompare(order.process_status) === 0) return;
      count++;
    });
    return count;

  }

  const getTotalDiscount = () => {
    let amount = 0;
    orders.forEach(order => {
      if(Constants.ORDER_PROCESS_STATUS.cancelled.localeCompare(order.process_status) === 0) return;
      amount += order.discount;
    });
    return amount;

  }

  const getGrandTotal = () => {
    let amount = 0;
    orders.forEach(order => {
      if(Constants.ORDER_PROCESS_STATUS.cancelled.localeCompare(order.process_status) === 0) return;
      amount += order.total_price;
    });
    return amount;

  }

  const getTableBody = () => {
    const tableCell = [];
    if(rootContext.userInfo.branches != null) {
      console.log(rootContext.userInfo.branches);
      tableCell.push(
        rootContext.userInfo.branches.map((branch, bIndex) => (
          rootContext.userInfo.delivery_partners.map((dPartner, dIndex) => (
            <TableRow key={bIndex + dIndex}>
              {(dIndex === 0) && (<TableCell rowSpan={rootContext.userInfo.delivery_partners.length}
                                             className={classes.tableCell} align={"left"}>{branch}</TableCell>)}
              <TableCell className={classes.tableCell} align="center">{dPartner}</TableCell>
              <TableCell className={classes.tableCell}
                         align="center">{getTotalNumberOfOrder(branch, dPartner)}</TableCell>
              <TableCell className={classes.tableCell}
                         align="center">{getTotalDiscountedAmount(branch, dPartner)}</TableCell>
              <TableCell className={classes.tableCell}
                         align="center">{getTotalPriceByDeliveryPartner(branch, dPartner)}</TableCell>
              {(dIndex === 0) && (<TableCell rowSpan={rootContext.userInfo.delivery_partners.length}
                                             className={classes.tableCell} align="center">{getTotalPrice(branch)}</TableCell>)}
            </TableRow>
          ))
        ))
      );

      tableCell.push(
        <TableRow>
          <TableCell className={classes.tableCell} align="left">{'Summary'}</TableCell>
          <TableCell className={classes.tableCell} align="center">{}</TableCell>
          <TableCell className={classes.tableCell} align="center">{getTotalOrderCount()}</TableCell>
          <TableCell className={classes.tableCell} align="center">{getTotalDiscount()}</TableCell>
          <TableCell className={classes.tableCell} colSpan={2} align="center">{getGrandTotal()}</TableCell>
        </TableRow>
      );
    }
    const tableBody = [];
    tableBody.push(
      <TableBody>
        {tableCell}
      </TableBody>
    );

    return tableBody;
  }



  return (
    <div style={{display: "block"}}>
      <div className={classes.printButton}>
        <ReactToPrint content={() => reportRef.current} trigger={() =>
          <Button color="secondary" variant={"outlined"}>
            Print
          </Button>
        }
        />
      </div>
      <Card className={classes.root} variant="outlined" ref={reportRef}>
        <CardContent>
          <div className={classes.headerText}>
            <Typography>
              <b>{rootContext.userInfo.name}</b>
              <span style={{fontSize: '.5rem'}}> powered by {process.env.REACT_APP_POWERED_BY}</span>
            </Typography>
            <Typography>
              <b>Report Generation TIme: </b> {Date()}
            </Typography>
            <Typography>
            <b>Type:</b> Daily Report
            </Typography>
          </div>
          <TableContainer className={classes.tableContainer} component={Paper}>
            <Table size="small" aria-label="a dense table">
              <TableHead className={classes.tableHeader}>
                <TableRow>
                  <StyledTableCell>{"Branch"}</StyledTableCell>
                  <StyledTableCell align="center">{'Delivery Partner'}</StyledTableCell>
                  <StyledTableCell align="right">{'Total Order'}</StyledTableCell>
                  <StyledTableCell align="center">{'Discounted Amount'}</StyledTableCell>
                  <StyledTableCell align="center">{'Total Price'}</StyledTableCell>
                  <StyledTableCell align="center">{'Branch Total'}</StyledTableCell>
                </TableRow>
              </TableHead>
              {getTableBody(props.order)}
            </Table>
          </TableContainer>
        </CardContent>
      </Card>
    </div>
  );
}


MiniReport.propTypes = {
  orderQueue: PropTypes.any.isRequired,
}