import React from "react";
import { withStyles } from '@material-ui/core/styles';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import {MENU_ENTRY, ORDER_STATUS, PLACE_ORDER, REPORT} from "../../utils/Strings";
import MenuManagement from "../MenuManagement/MenuManagement";
import {RootContext} from "../../libs/ContextLibs";
import OrderManagement from "../OrderManagement/OrderManagement";
import OrderQueueManagement from "../OrderQueueManagement/OrderQueueManagement";
import {Constants} from "../../utils/Constants";
import {getMenuApi, getOrderQueueByTimeSegment, getUserInfo} from "../../api/LambdaApi";
import {getDayStartEpochTimeMillis} from "../../utils/misc";
import Paper from "@material-ui/core/Paper";
import AlertSound from '../../res/audio/alertTone.mp3';
import ReportHome from "../Reporting/ReportHome";

const styles = () => ({
  root: {
    flexGrow: 1,
    paddingBottom: 16,
    minHeight: '80vh',
  },
});

export class Home extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      tabNo: 0,
      menuList: [],
      orderQueue: {},
      initialDataLoaded: false,
      userInfo: {
        user_level: 2,
      },
    }
    this.soundInterval = null;
  }

  PAGE_MARKER = {
    menu_management: 2,
    order_management: 1,
    order_queue_management: 0,
    report: 3,
  }

  handleChange = (event, newValue) => {
    this.setState({
      tabNo: newValue
    });
  };

  updateOrderQueue = (newData) => {
    const str = JSON.stringify(this.state.orderQueue);
    const newObj = JSON.parse(str);

    newData.forEach((row) => {
      newObj[row.order_timestamp_local] = row;
    });

    this.setState({
      orderQueue: newObj,
    }, () => {
      if(parseInt(this.state.userInfo.user_level) === 2){
        this.playSound();
      }
    });
  }

  componentDidMount() {
    if(this.state.initialDataLoaded === false) {
      this.setState({
        initialDataLoaded: true
      });
      this.getUserInfo().then(() => {
          this.getMenuItems().then();
          this.getLastDayHourOrderList().then();
        });
    }
  }

  shouldUpdateMenu = () => {
    this.getMenuItems().then();
  }

  getUserInfo = async () => {
    try {
      let result = {};
      if(process.env.NODE_ENV === Constants.DEVELOPMENT_MODE){
        result = dummyUserInfo;
      } else {
        result = await getUserInfo();
        console.log(result);
      }
      this.setState({
        userInfo: result
      });
    } catch (e) {
      console.log('User Info err: ', e);
      alert('User info download failed! Please contact admin!');
    }
  }

  playSound = () => {
    if(!this.soundInterval){
      let audio = new Audio(AlertSound);
      audio.volume = 1;

      this.soundInterval = setInterval(() => {
        if(this.isAnyOrderNotStarted()) {
          audio.play();
        } else {
          clearInterval(this.soundInterval);
          this.soundInterval = null;
        }
      }, 3000);
    }
  }

  isAnyOrderNotStarted = () => {
    let ret = false;
    Object.keys(this.state.orderQueue).forEach(key => {
      if(Constants.ORDER_PROCESS_STATUS.notStarted.localeCompare(this.state.orderQueue[key].process_status) === 0) {
        ret = true;
      }
    });
    return ret;
  }

  getLastDayHourOrderList = async () => {
    let endTime = Date.now();
    let startTime = endTime - Constants.TIME_IN_MILLISECOND.TWO_HOUR;
    const dayStartTime = getDayStartEpochTimeMillis();
    const tempOrderQueue = {};
    try {
      if (process.env.NODE_ENV === Constants.DEVELOPMENT_MODE) {
        const result = dummyOrderQueue;
        result.forEach(row => tempOrderQueue[row.order_timestamp_local] = row);
      } else {
        for (let t = 1; t < 13 && startTime > dayStartTime; t++) {
          const params = {
            body: {
              start_time: startTime,
              end_time: endTime,
              parent_user_id: this.state.userInfo.parent_user_id,
            }
          };
          const result = await getOrderQueueByTimeSegment(params);
          result.forEach(row => tempOrderQueue[row.order_timestamp_local] = row);
          endTime = endTime - Constants.TIME_IN_MILLISECOND.TWO_HOUR;
          startTime = startTime - Constants.TIME_IN_MILLISECOND.TWO_HOUR;
        }
      }
      this.setState({
        orderQueue: tempOrderQueue
      }, () => {
        if(parseInt(this.state.userInfo.user_level) === 2){
          this.playSound();
        }
      });
      console.log('After 24 hour Order Queue: ', tempOrderQueue);

    } catch(error){
      console.log('24 hour order: ', error);
      alert('Sorry! Couldn\'nt download Order queue');
    }
  }

  getLastTwoHourOrder = async () => {
    let endTime = Date.now();
    let startTime = endTime - Constants.TIME_IN_MILLISECOND.TWO_HOUR;
    let result = {};
    try {
      if (process.env.NODE_ENV === Constants.DEVELOPMENT_MODE) {
        result = [];
      } else {
        const params = {
          body: {
            start_time: startTime,
            end_time: endTime,
            parent_user_id: this.state.userInfo.parent_user_id,
          }
        };
        result = await getOrderQueueByTimeSegment(params);
      }
      if(result.length > 0) this.updateOrderQueue(result);
      // console.log('After Last minute Order Queue: ', result);

    } catch(error){
      console.log('Last minute order: ', error);
      alert('Sorry! Couldn\'nt download Order queue');
    }
  };

  getMenuItems = async () => {
    const params = {
      body: {
        parent_user_id: this.state.userInfo.parent_user_id,
      }
    };
    try {
      if(process.env.NODE_ENV === Constants.DEVELOPMENT_MODE){
        const dummyData = [
          this.createData("11", "Naga Burger", "FastFood", 400),
          this.createData("01", "Spicy chicken Burrito", "FastFood", 355),
          this.createData("02", "Vegetable pizza 8 inch", "FastFood", 350),
          this.createData("03", "Vegetable pizza 8 inch", "FastFood", 350),
          this.createData("123", "Vegetable pizza 8 inch", "FastFood", 350),
          this.createData("223", "Vegetable pizza 8 inch", "FastFood", 350),
          this.createData("A1-23", "Vegetable pizza 8 inch", "FastFood", 350),
        ];

        this.setState({
          menuList: dummyData.sort((a, b) => {
            let menuCodeA = a.menu_code.toUpperCase();
            let menuCodeB = b.menu_code.toUpperCase();
            if(menuCodeA < menuCodeB) return -1;
            if(menuCodeA > menuCodeB) return 1;
            return 0;
          })
        });
      } else {
        const result = await getMenuApi(params);
        console.log("menuItems", result);
        this.setState({
          menuList: result
        });
      }
    } catch (e) {
      alert(e);
    }
  };

  createData = (menu_code, name, category, price) => {
    return {menu_code, name, category, price};
  }

  getUserLevel = () => {
    return parseInt(this.state.userInfo.user_level);
  }

  render() {
    const { classes } = this.props;

    return (
      <Paper className={classes.root} elevation={1}>
        <RootContext.Provider value={{
            menuList: this.state.menuList,
            shouldUpdateMenu: this.shouldUpdateMenu,
            userInfo: this.state.userInfo
          }}>
          <Tabs
            value={this.state.tabNo}
            onChange={this.handleChange}
            indicatorColor="primary"
            textColor="primary"
            centered
          >
            {(this.getUserLevel() > 0) && (<Tab label={ORDER_STATUS}/>)}
            {(this.getUserLevel() === 1 || this.getUserLevel() === 3) && (<Tab label={PLACE_ORDER}/>)}
            {(this.getUserLevel() === 1) && (<Tab label={MENU_ENTRY}/>)}
            {(this.getUserLevel() === 1) && (<Tab label={REPORT}/>)}
          </Tabs>
          {
            (this.state.tabNo === this.PAGE_MARKER.menu_management) &&
            <MenuManagement />
          }
          {
            (this.state.tabNo === this.PAGE_MARKER.order_queue_management) &&
            <OrderQueueManagement
              orderQueue={this.state.orderQueue}
              getLastTwoHourOrder={this.getLastTwoHourOrder}
              refreshOrderQueue={this.getLastDayHourOrderList}
            />
            // <div></div>
          }
          {
            (this.state.tabNo === this.PAGE_MARKER.order_management) &&
            <OrderManagement
              updateOrderQueue={this.getLastTwoHourOrder}
            />
          }
          {
            (this.state.tabNo === this.PAGE_MARKER.report) &&
            <ReportHome
              orderQueue={this.state.orderQueue}
            />
          }
        </RootContext.Provider>
      </Paper>
    );
  }

}

export default withStyles(styles, {withTheme: true}) (Home);

