import React, {useEffect} from 'react'
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import TableContainer from "@material-ui/core/TableContainer";
import Paper from "@material-ui/core/Paper";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell";
import { MENU_CODE, NAME, ORDER_ID, SERIAL_NO } from '../../utils/Strings';
import CardActions from "@material-ui/core/CardActions";
import Button from "@material-ui/core/Button";
import TableBody from "@material-ui/core/TableBody";
import {makeStyles, withStyles} from "@material-ui/core/styles";
import PropTypes from "prop-types";
import {useRootContext} from "../../libs/ContextLibs";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from '@material-ui/core/DialogActions';
import Typography from "@material-ui/core/Typography";
import {Constants} from "../../utils/Constants";

const useStyles = makeStyles({
  root: {
    minWidth: 275,
    margin: 16,
  },
  maxHeight: {
    maxHeight: 200,
  },
  textField: {
    float: 'left',
    padding: 8
  },
  tableCell: {
    borderRight: '1px solid lightgrey',
    paddingRight: 16,
  }
});

const StyledTableCell = withStyles(() => ({
  head: {
    fontSize: 12,
    fontWeight: "bold"
  },
}))(TableCell);

export default function SingleOrderComponent(props){
  const classes = useStyles();
  const rootContext = useRootContext();
  const [elapsedTime, setElapsedTime] = React.useState('0');
  const [isDialogOpen, setIsDialogOpen] = React.useState(false);
  const [dialog, setDialog] = React.useState([]);

  useEffect(() => {
    const interval = setInterval(() => {
      calculateElapsedTime(props.order.order_timestamp_local);
    }, 1000);

    return () => clearInterval(interval);
  }, [props.order.order_timestamp_local]);

  const onDialogClose = () => {
    setIsDialogOpen(false);
  }

  const getNameFromMenuCode = (menu_code) => {
    const menu = rootContext.menuList.find(item => item.menu_code === menu_code);
    if(menu != null) return menu.name;
  }

  const getTimeFromEpochTime = (epochTime) => {
    const date = new Date(epochTime);
    return date.toLocaleTimeString().replace(/([\d]+:[\d]{2})(:[\d]{2})(.*)/, "$1$3");
  }

  const calculateElapsedTime = (epochTime) => {
    let timeElapsed = (Date.now() - parseInt(epochTime)) / 1000;
    const hour = Math.floor(timeElapsed / 3600);
    timeElapsed -= hour * 3600;
    const min = Math.floor(timeElapsed / 60);
    timeElapsed -= min * 60;
    const sec = Math.floor(timeElapsed);
    setElapsedTime( hour + ':' + min + ':' + sec);
  }

  const getElapsedTime = () => {
    if(isStatusAndCancelButtonDisabled() === true){
      return getTimeFromEpochTime(props.order.update_order_timestamp_local);
    }
    return elapsedTime;
  }

  const isStatusAndCancelButtonDisabled = () => {
    if(Constants.ORDER_PROCESS_STATUS.delivered.localeCompare(props.order.process_status) === 0) return true;
    if(Constants.ORDER_PROCESS_STATUS.cancelled.localeCompare(props.order.process_status) === 0) return true;
    return false;
  }

  const openDialog = () => {
    const newDialog = [];
    newDialog.push(
      <DialogTitle id="form-dialog-title">{'Price & Others'}</DialogTitle>
    );
    newDialog.push(
      <DialogContent>
        <Typography>
          Total Price: {props.order.total_price}
        </Typography>
        <Typography>
          Name: {props.order.customer_name}
        </Typography>
        <Typography>
          Contact: {props.order.customer_contact}
        </Typography>
        <Typography>
          Address: {props.order.delivery_address}
        </Typography>
        <Typography>
          Comment: {props.order.comment}
        </Typography>
      </DialogContent>
    );
    newDialog.push(
      <DialogActions>
        <Button onClick={onDialogClose} color="primary">
          Close
        </Button>
      </DialogActions>
    );
    setDialog(newDialog);
    setIsDialogOpen(true);
  }



  const getTableBody = () => {
    const tableCell = [];
    tableCell.push(
      props.order.menu_code_list.map((value, index) => (
        <TableRow key={index}>
          {(index === 0) && (<TableCell className={classes.tableCell} rowSpan={props.order.menu_code_list.length} align="left">{props.serial}</TableCell>)}
          <TableCell align={"center"}>{value}</TableCell>
          <TableCell align="center">{getNameFromMenuCode(value)}</TableCell>
          <TableCell className={classes.tableCell} align="center">{props.order.quantity_list[index]}</TableCell>
          {(index === 0) && (<TableCell rowSpan={props.order.menu_code_list.length} align="center">{props.order.client_order_id}</TableCell>)}
          
          {(index === 0) && (<TableCell rowSpan={props.order.menu_code_list.length} align="center">{props.order.delivery_partner}</TableCell>)}
          {(index === 0) && (<TableCell rowSpan={props.order.menu_code_list.length} align="center">{props.order.branch}</TableCell>)}
          {(index === 0) && (<TableCell rowSpan={props.order.menu_code_list.length} align="center">
                              {getTimeFromEpochTime(props.order.order_timestamp_local)}
                            </TableCell>)}
          {(index === 0) && (<TableCell rowSpan={props.order.menu_code_list.length} align="center">{getElapsedTime()}</TableCell>)}
          {(index === 0) && (<TableCell rowSpan={props.order.menu_code_list.length} style={{color: 'green', fontWeight: 'bold'}}
                                        align="center">{props.order.process_status}</TableCell>)}
          {(index === 0) && (<TableCell rowSpan={props.order.menu_code_list.length} align="center">
            <Button variant={"outlined"} color={"default"} size={"small"}
                    onClick={openDialog}>
              Alohamora
            </Button>
          </TableCell>)}
        </TableRow>
      ))
    );

    const tableBody = [];
    tableBody.push(
      <TableBody>
        {tableCell}
      </TableBody>
    );
    return tableBody;
  }

  const onCancelOrder = () => {
    const newDialog = [];
    newDialog.push(
      <DialogTitle id="form-dialog-title">{'Cancel Order'}</DialogTitle>
    );
    newDialog.push(<DialogContent>
        <Typography>
          Are you sure to cancel?
          You are gonna loose a whole lot of money!
        </Typography>
      </DialogContent>
    );
    newDialog.push(
      <DialogActions>
        <Button onClick={cancelOrder} color="secondary">
          Cancel Please!
        </Button>
      </DialogActions>
    );
    setDialog(newDialog);
    setIsDialogOpen(true);
  }

  const onStatusChange = () => {
    const status = props.order.process_status;
    let nextStatus = '';
    if(status.localeCompare(Constants.ORDER_PROCESS_STATUS.notStarted) === 0){
      nextStatus = Constants.ORDER_PROCESS_STATUS.preparing;
    }
    if(status.localeCompare(Constants.ORDER_PROCESS_STATUS.preparing) === 0){
      nextStatus = Constants.ORDER_PROCESS_STATUS.readyToDeliver;
    }
    if(status.localeCompare(Constants.ORDER_PROCESS_STATUS.readyToDeliver) === 0){
      nextStatus = Constants.ORDER_PROCESS_STATUS.delivered;
    }

    props.updateStatus(props.order, nextStatus);
  }

  const cancelOrder = () => {
    setIsDialogOpen(false);
    props.updateStatus(props.order, Constants.ORDER_PROCESS_STATUS.cancelled);
  }

  const getStatusButtonText = () => {
    const status = props.order.process_status;
    if(status.localeCompare(Constants.ORDER_PROCESS_STATUS.notStarted) === 0){
        return Constants.STATUS_BUTTON_TEXT.notStarted;
    }
    if(status.localeCompare(Constants.ORDER_PROCESS_STATUS.preparing) === 0){
      return Constants.STATUS_BUTTON_TEXT.preparing;
    }
    if(status.localeCompare(Constants.ORDER_PROCESS_STATUS.readyToDeliver) === 0){
      return Constants.STATUS_BUTTON_TEXT.readyToDeliver;
    }
    if(status.localeCompare(Constants.ORDER_PROCESS_STATUS.delivered) === 0){
      return Constants.STATUS_BUTTON_TEXT.delivered;
    }
    if(status.localeCompare(Constants.ORDER_PROCESS_STATUS.cancelled) === 0){
      return Constants.STATUS_BUTTON_TEXT.cancelled;
    }
  }


  return (
    <div>
      <Card className={classes.root} variant="outlined">
        <CardContent>
          <TableContainer component={Paper} className={classes.maxHeight}>
            <Table stickyHeader size="small" aria-label="a dense table">
              <TableHead>
                <TableRow>
                  <StyledTableCell>{SERIAL_NO}</StyledTableCell>
                  <StyledTableCell align="center">{MENU_CODE}</StyledTableCell>
                  <StyledTableCell align="center">{NAME}</StyledTableCell>
                  <StyledTableCell align="center">{'Quantity'}</StyledTableCell>
                  <StyledTableCell align="center">{ORDER_ID}</StyledTableCell>
                  <StyledTableCell align="center">{'Delivery Partner'}</StyledTableCell>
                  <StyledTableCell align="center">{'Branch'}</StyledTableCell>
                  <StyledTableCell align="center">{'Order Time'}</StyledTableCell>
                  <StyledTableCell align="center">{'Elapsed Time'}</StyledTableCell>
                  <StyledTableCell align="center">{'Current Status'}</StyledTableCell>
                  <StyledTableCell align="center">{'Price & Others'}</StyledTableCell>
                </TableRow>
              </TableHead>
              {getTableBody(props.order)}
            </Table>
          </TableContainer>
        </CardContent>
        <CardActions>
          <Button variant={'outlined'} color={'primary'} size="small" disabled={isStatusAndCancelButtonDisabled()} onClick={onStatusChange}>{getStatusButtonText()}</Button>
          {
            (parseInt(rootContext.userInfo.user_level) !== 2) &&
            (<Button variant={'contained'} color={'secondary'} size="small" disabled={isStatusAndCancelButtonDisabled()}
                   onClick={onCancelOrder}>Cancel</Button>)
          }
        </CardActions>
      </Card>
      <Dialog open={isDialogOpen} style={{minWidth: '400px'}} onClose={onDialogClose} aria-labelledby="form-dialog-title">
        {dialog}
      </Dialog>
    </div>
  )
}

SingleOrderComponent.propTypes = {
  order: PropTypes.any.isRequired,
  updateStatus: PropTypes.func.isRequired
}
