import React, { Component } from "react";
import { withStyles } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Button from "@material-ui/core/Button";
import RestSoftLogo from '../res/images/restsoft.png';
import {AppContext} from "../libs/ContextLibs";
import { Auth } from "aws-amplify";
import { Link } from "react-router-dom";
import { Constants } from "../utils/Constants";
import LogoutLogo from '../res/images/logout.png';
import AboutLogo from '../res/images/about.png';

const styles = (theme) => ({
  root: {
    flexGrow: 1,
    backgroundColor: "#383838",
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
});

class AppBarComponent extends Component {
  constructor(props) {
    super(props);

    this.state = {};
  }

  handleLogOut = async () => {
    const { userHasAuthenticated } = this.context;
    await Auth.signOut();
    userHasAuthenticated(false);
  };

  render() {
    const { isAuthenticated } = this.context;
    const { classes } = this.props;
    return (
      <div className={classes.root}>
        <AppBar position="static" color={'transparent'}>
          <Toolbar>
            {/*<IconButton*/}
            {/*    edge="start"*/}
            {/*    className={classes.menuButton}*/}
            {/*    color="inherit"*/}
            {/*    aria-label="menu"*/}
            {/*>*/}
            {/*    <MenuIcon/>*/}
            {/*</IconButton>*/}
            <Button component={Link} to={Constants.LINKS.HOME}>
              <img style={{height: '50px'}} src={RestSoftLogo} alt={"RestSoft"}/>
            </Button>
            <div className={classes.title}></div>
            <a href={process.env.REACT_APP_ABOUT_URL} target={"_blank"} rel={"noopener noreferrer"}>
              <Button>
              <img style={{height: '30px'}} src={AboutLogo} alt={"About"}/>
            </Button>
            </a>
            {isAuthenticated ? (
              <Button color="inherit" onClick={this.handleLogOut}>
                <img style={{height: '30px'}} src={LogoutLogo} alt={"Logout"}/>
              </Button>
            ) : null}
          </Toolbar>
        </AppBar>
      </div>
    );
  }
}

AppBarComponent.contextType = AppContext;

export default withStyles(styles, { withTheme: true })(AppBarComponent);
