import React from "react";
import { Route, Switch, Redirect } from "react-router-dom";
import Home from "./pages/HomePage/Home";
import LoginPage from "./pages/AuthUI/LoginPage";
import ProtectedRoute from "./libs/ProtectedRoute";
import { Constants } from "./utils/Constants";

export default function Routes() {
  return (
    <Switch>
      <Redirect exact from={Constants.SLASH} to={Constants.LINKS.LOGIN} />
      <Route path={Constants.LINKS.LOGIN} component={LoginPage} />
      <ProtectedRoute path={Constants.LINKS.HOME} component={Home} />
    </Switch>
  );
}
