import React, {Fragment} from 'react';
import Container from "@material-ui/core/Container";
import Grid from "@material-ui/core/Grid";
import {ADD_ORDER} from "../../utils/Strings";
import Button from "@material-ui/core/Button";
import {makeStyles} from "@material-ui/core/styles";
import AddOrderDialog from "./AddOrderDialog";
import OrderList from "./OrderList";
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import TextField from "@material-ui/core/TextField";
import {Constants} from "../../utils/Constants";
import {placeOrderApi} from "../../api/LambdaApi";
import PropTypes from 'prop-types';
import {useRootContext} from "../../libs/ContextLibs";


const useStyles = makeStyles(theme => ({
  option: {
    fontSize: 15,
    '& > span': {
      marginRight: 10,
      fontSize: 18,
    },
  },
  autocompleteOutline: {
    border: '1px solid grey',
    padding: '16px',
  },
  formControl: {
    margin: theme.spacing(1),
    marginLeft:theme.spacing(3),
    minWidth: 120,
  },
  textFormControl: {
    display: "block",
    margin: theme.spacing(2),
    marginLeft:theme.spacing(3),
  }
}));


export default function OrderManagement(props) {
  const classes = useStyles();
  const rootContext = useRootContext();

  const deliveryOptions = rootContext.userInfo.delivery_partners;
  const branchOptions = rootContext.userInfo.branches;

  const [items, setItems] = React.useState([]);
  const [quantity, setQuantity] = React.useState([]);
  const [isOpenAddOrderDialog, toggleAddOrderDialog] = React.useState(false);
  const [deliveryPartner, setDeliveryPartner] = React.useState(deliveryOptions[0]);
  const [branch, setBranch] = React.useState(branchOptions[0]);
  const [customerName, setCustomerName] = React.useState('');
  const [clientOrderId, setClientOrderId] = React.useState('');
  const [customerContact, setCustomerContact] = React.useState('');
  const [deliveryAddress, setDeliveryAddress] = React.useState('');
  const [comment, setComment] = React.useState('');

  let totalPrice = 0;
  let discount = 0;

  const handleCloseAddOrderDialog = () => {
    toggleAddOrderDialog(false);
  }

  const onItemSelected = (menu_code, itemQuantity) => {
    const newItems = [...items, menu_code];
    setItems(newItems);
    const newQuantity = [...quantity, itemQuantity];
    setQuantity(newQuantity);
  }

  const updateTotalPrice = (tPrice) => {
    totalPrice = tPrice;
  }

  const updateDiscount = (dis) => {
    discount = dis;
  }

  const removeItemFromOrder = (index) => {
    let newItems = [...items];
    let newQuantity = [...quantity];
    newItems.splice(index, 1);
    newQuantity.splice(index, 1);
    setItems(newItems);
    setQuantity(newQuantity);
  }

  const resetMe = () => {
    setItems([]);
    setQuantity([]);
    setClientOrderId('');
    setCustomerName('');
    setCustomerContact('');
    setDeliveryAddress('');
    setComment('');
  }

  const placeOrder = async () => {
    const params = {
      body: {
        menu_code_list: items,
        order_timestamp_local: Date.now(),
        quantity_list: quantity,
        delivery_partner: deliveryPartner,
        branch: branch,
        comment: comment,
        delivery_address: deliveryAddress,
        client_order_id: clientOrderId,
        customer_name: customerName,
        customer_contact: customerContact,
        total_price: totalPrice,
        discount: discount,
        process_status: Constants.ORDER_PROCESS_STATUS.notStarted,
        parent_user_id: rootContext.userInfo.parent_user_id,
      }
    };
    console.log('Order: ', params.body);
    try {
      await placeOrderApi(params);
      props.updateOrderQueue();
      resetMe();
    } catch (error) {
      console.log("error", error);
      alert('Sorry! Something went wrong. Order could not be placed.')
    }
  }

  const isPlaceOrderDisabled = () => {
    if(items.length === 0) return true;
    return false;
  }

  return (
    <Fragment>
      <Grid
        container
        direction="row"
        justify="space-between"
        alignItems="center"
        spacing={1}
        className="full-height"
      >
        <Grid Item xs={5}>
          <div>
            <FormControl variant="outlined" className={classes.formControl}>
              <Button variant={"outlined"} color={"secondary"}
                      onClick={() => toggleAddOrderDialog(true)}
                      >
                {ADD_ORDER}
              </Button>
              <AddOrderDialog
                open={isOpenAddOrderDialog}
                handleClose={handleCloseAddOrderDialog}
                onItemSelected={onItemSelected}
              />
            </FormControl>
          </div>
          <FormControl variant="standard" className={classes.formControl}>
            <InputLabel>Delivery Partner</InputLabel>
            <Select
              value={deliveryPartner}
              onChange={(event) => setDeliveryPartner(event.target.value)}
              autoWidth
            >
              {
                deliveryOptions.map(option => (
                  <MenuItem value={option}>{option}</MenuItem>
                ))
              }
            </Select>
          </FormControl>
          <FormControl variant="standard" className={classes.formControl}>
            <InputLabel >Branch</InputLabel>
            <Select
              value={branch}
              onChange={(event) => setBranch(event.target.value)}
              autoWidth
            >
              {
                branchOptions.map(option => (
                  <MenuItem value={option}>{option}</MenuItem>
                ))
              }
            </Select>
          </FormControl>
          <FormControl variant="outlined" className={classes.textFormControl}>
            <TextField
              className="text-field"
              label={"Order ID"}
              value={clientOrderId}
              onChange={(event) =>
                setClientOrderId(event.target.value)
              }
            />
          </FormControl>
          <FormControl variant="outlined" className={classes.textFormControl}>
            <TextField
              className="text-field"
              label={"Customer Name"}
              value={customerName}
              onChange={(event) =>
                setCustomerName(event.target.value)
              }
            />
          </FormControl>
          <FormControl variant="outlined" className={classes.textFormControl}>
            <TextField
              className="text-field"
              label={"Delivery Address"}
              value={deliveryAddress}
              onChange={(event) =>
                setDeliveryAddress(event.target.value)
              }
            />
          </FormControl>
          <FormControl variant="outlined" className={classes.textFormControl}>
            <TextField
              className="text-field"
              label={"Contact No"}
              value={customerContact}
              type="number"
              onChange={(event) =>
                setCustomerContact(event.target.value)
              }
            />
          </FormControl>
          <FormControl variant="outlined" className={classes.textFormControl}>
            <TextField
              className="text-field"
              label={"Comment"}
              value={comment}
              onChange={(event) =>
                setComment(event.target.value)
              }
            />
          </FormControl>
          <FormControl variant="standard" className={classes.formControl} style={{paddingTop: '16px'}}>
            <Button variant={"contained"} color={"secondary"}
                    disabled={isPlaceOrderDisabled()}
                    onClick={placeOrder}
            >
              {"Place order"}
            </Button>
          </FormControl>
        </Grid>
        <Grid Item xs={7}>
          <Container className="basic-margin-padding">
            <OrderList
              orderedMenuCodeList={items}
              orderedQuantityList={quantity}
              updateTotalPrice={updateTotalPrice}
              removeItemFromOrder={removeItemFromOrder}
              updateDiscount={updateDiscount}
            />
          </Container>
        </Grid>
      </Grid>
    </Fragment>
  );
}

OrderManagement.propTypes = {
  updateOrderQueue: PropTypes.func.isRequired
}
