import React from 'react';
import MiniReport from "./MiniReport";
import PropTypes from "prop-types";
import {makeStyles} from "@material-ui/core/styles";
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import OrderInsight from "./OrderInsightReport";


const useStyles = makeStyles({
  root: {
    marginBottom: 16,
    height: 'auto',
  },
  control: {
    minWidth: 275,
    margin: 16,
    marginLeft: 48,
    marginRight: 48,
  }
});

export default function ReportHome(props) {
  const Reports = {
    summary: 'summary',
    order_insight: 'order_insight'
  };

  const [reportType, setReportType] = React.useState(Reports.summary);
  const classes = useStyles();



  const handleReportChange = (event) => {
    console.log('Report Type', event.target.value);
    setReportType(event.target.value);
  }

  return(
    <div className={classes.root}>
      <div className={classes.control}>
        <FormControl component="fieldset">
          <FormLabel component="legend">Select Report Category</FormLabel>
          <RadioGroup aria-label="Report Type" name="reports" value={reportType} onChange={handleReportChange}>
            <FormControlLabel value={Reports.summary} control={<Radio color={"primary"}/>} label="Summary"/>
            <FormControlLabel value={Reports.order_insight} control={<Radio color={"primary"}/>} label="Order Insight" />
          </RadioGroup>
        </FormControl>
      </div>
      <div>
        {(reportType === Reports.summary) && <MiniReport orderQueue={props.orderQueue} />}
        {(reportType === Reports.order_insight) && <OrderInsight orderQueue={props.orderQueue} />}
      </div>
    </div>
  );
}

ReportHome.propTypes = {
  orderQueue: PropTypes.any.isRequired,
}