import React from 'react';
import Paper from "@material-ui/core/Paper";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell";
import {RATE, QUANTITY, MENU_CODE, NAME, PRICE} from "../../utils/Strings";
import TableBody from "@material-ui/core/TableBody";
import IconButton from "@material-ui/core/IconButton";
import {Delete} from "@material-ui/icons";
import TableContainer from "@material-ui/core/TableContainer";
import PropTypes from 'prop-types';
import {useRootContext} from "../../libs/ContextLibs";
import {makeStyles} from "@material-ui/core/styles";
import TextField from "@material-ui/core/TextField";

const useStyles = makeStyles({
  maxHeight: {
    height: 300
  },
  textField: {
    float: 'right',
    paddingTop: 16,
    paddingBottom: 16,
    marginLeft: 16,
  }
});


export default function OrderList(props) {
  const classes = useStyles();
  const rootContext = useRootContext();
  const [discount, setDiscount] = React.useState(0);
  let totalPrice = 0;
  const handleDelete = (index) => {
    props.removeItemFromOrder(index);
    console.log(index);
  }

  const getMenuFromOrderedList = () => {
    const returnMenu = [];
    totalPrice = 0;
    for(let i = 0; i < props.orderedMenuCodeList.length; i++){
      const item = rootContext.menuList.find(element => element.menu_code === props.orderedMenuCodeList[i]);
      const tableItem = {
        menu_code: item.menu_code,
        name: item.name,
        quantity: props.orderedQuantityList[i],
        rate: item.price,
        price: item.price * props.orderedQuantityList[i]
      }
      totalPrice += tableItem.price;
      returnMenu.push(tableItem);
    }
    let discountedAmount = 0;
    if(Number.isNaN(discount) === false && discount > 0) {
      const temp = totalPrice;
      totalPrice = Math.floor(totalPrice - (totalPrice * discount) / 100);
      discountedAmount = temp - totalPrice;
    }
    props.updateDiscount(Number.isNaN(discount) ? 0 : discountedAmount);
    props.updateTotalPrice(totalPrice);

    return returnMenu;
  }

  return (
    <div>
      <TableContainer component={Paper} className={classes.maxHeight}>
        <Table stickyHeader size="small" aria-label="a dense table">
          <TableHead className="table-head">
            <TableRow>
              <TableCell>{MENU_CODE}</TableCell>
              <TableCell align="center">{NAME}</TableCell>
              <TableCell align="center">{QUANTITY}</TableCell>
              <TableCell align="right">{RATE}</TableCell>
              <TableCell align="right">{PRICE}</TableCell>
              <TableCell align="center"></TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {getMenuFromOrderedList().map((row, index) => (
              <TableRow key={index}>
                <TableCell align="left"> {row.menu_code} </TableCell>
                <TableCell align="center">{row.name}</TableCell>
                <TableCell align="center">{row.quantity}</TableCell>
                <TableCell align="right">{row.rate}</TableCell>
                <TableCell align="right">{row.price}</TableCell>
                <TableCell align="center">
                  <IconButton
                    aria-label="delete"
                    component="span"
                    onClick={() => {
                      handleDelete(index);
                    }}
                  >
                    <Delete />
                  </IconButton>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
      <div className={classes.textField}>
        <TextField
          variant={'outlined'}
          label={'Discount %'}
          size={"small"}
          value={discount}
          onChange={(event) => setDiscount(parseInt(event.target.value))}
          type={"number"}
        />
        <TextField
          variant={'outlined'}
          label={'Grand Total'}
          size={"small"}
          InputProps={{
            readOnly: true,
          }}
          value={totalPrice}
        />
      </div>
    </div>
  );
}


OrderList.propTypes = {
  orderedMenuCodeList: PropTypes.array.isRequired,
  orderedQuantityList: PropTypes.array.isRequired,
  updateTotalPrice: PropTypes.func.isRequired,
  removeItemFromOrder: PropTypes.func.isRequired,
  updateDiscount: PropTypes.func.isRequired,
}
