import React, { Component, Fragment } from "react";
import Grid from "@material-ui/core/Grid";
import Container from "@material-ui/core/Container";
import MenuListViewComponent from "./MenuListViewComponent";
import MenuEntryFormComponent from "./MenuEntryFormComponent";
import "./menu_style.css";
import {RootContext} from "../../libs/ContextLibs";

class MenuManagement extends Component {

  render() {
    return (
      <Fragment>
        <Grid
          container
          direction="row"
          justify="space-between"
          alignItems="center"
          spacing={1}
          className="full-height"
        >
          <Grid Item xs={5}>
            <Container className="basic-margin-padding">
              <MenuEntryFormComponent
                userInfo={this.context.userInfo}
                shouldUpdateMenu={this.context.shouldUpdateMenu}
              />
            </Container>
          </Grid>
          <Grid Item xs={7}>
            <Container className="basic-margin-padding">
              <MenuListViewComponent
                menuList={this.context.menuList}
                shouldUpdateMenu={this.context.shouldUpdateMenu}
                userInfo={this.context.userInfo}
              />
            </Container>
          </Grid>
        </Grid>
      </Fragment>
    );
  }
}

MenuManagement.contextType = RootContext;

export default MenuManagement;
