import React from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import {ADD_ITEM, MENU_CODE, NAME, PRICE} from "../../utils/Strings";
import TableContainer from "@material-ui/core/TableContainer";
import Paper from "@material-ui/core/Paper";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell";
import TableBody from "@material-ui/core/TableBody";
import {makeStyles} from "@material-ui/core/styles";
import {useRootContext} from "../../libs/ContextLibs";
import PropTypes from 'prop-types';


const useStyles = makeStyles({
  maxHeight: {
    height: 300
  },
  textField: {
    float: 'left',
    padding: 8
  }
});

export default function AddOrderDialog(props) {
  const classes = useStyles();
  const getDisabledStatus = () => {
    if(menu_code === '' || quantity < 1) return true;
    return false;
  }

  const rootContext = useRootContext();
  const [menu_code, setMenuCode] = React.useState('');
  const [quantity, setQuantity] = React.useState(1);
  const [searchText, setSearchText] = React.useState('');

  const onClickSelect = (menu_code, name) => {
    setMenuCode(menu_code);
    setSearchText(name);
  }

  const onSearchTextChange = (event) => {
    setSearchText(event.target.value);
  }

  const onDialogClose = () => {
    setMenuCode('');
    setQuantity(1);
    setSearchText('');
    props.handleClose();
  }

  const onAddClicked = () => {
    props.onItemSelected(menu_code, quantity);
    onDialogClose();
  }

  return (
    <Dialog open={props.open} onClose={onDialogClose} aria-labelledby="form-dialog-title">
      <DialogTitle id="form-dialog-title">{ADD_ITEM}</DialogTitle>
      <DialogContent>
        <div className={classes.textField}>
          <TextField
            autoFocus
            margin="dense"
            id="name"
            label="Search"
            value={searchText}
            variant={"outlined"}
            onChange={(event) => onSearchTextChange(event)}
          />
        </div>
        <div className={classes.textField}>
          <TextField
            margin="dense"
            id="quantity"
            label="Quantity"
            type={'number'}
            variant={"outlined"}
            value={quantity}
            onChange={(event) => setQuantity(event.target.value === '' ? 0 : parseInt(event.target.value))}
          />
        </div>
        <div>
          <TableContainer component={Paper} className={classes.maxHeight}>
            <Table stickyHeader size="small" aria-label="a dense table">
              <TableHead className="table-head">
                <TableRow>
                  <TableCell>{MENU_CODE}</TableCell>
                  <TableCell align="center">{NAME}</TableCell>
                  <TableCell align="right">{PRICE}</TableCell>
                  <TableCell align="right"></TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {rootContext.menuList.sort((a, b) => {
                  if(a.menu_code.length > b.menu_code.length) return 1;
                  else if(a.menu_code.length < b.menu_code.length) return -1;
                  else {
                    if(a.menu_code > b.menu_code) return 1;
                    if(a.menu_code < b.menu_code) return -1;
                  }
                  return 0;
                }).map((row, index) => (
                  (row.menu_code.toLowerCase().includes(searchText.toLocaleLowerCase()) ||
                    row.name.toLowerCase().includes(searchText.toLowerCase()) ||
                    row.price === parseInt(searchText)) &&
                  (<TableRow key={index}>
                    <TableCell align="left"> {row.menu_code} </TableCell>
                    <TableCell align="center">{row.name}</TableCell>
                    <TableCell align="right">{row.price}</TableCell>
                    <TableCell align="center">
                      <Button variant={"outlined"} color={"default"} size={"small"}
                              onClick={() => {onClickSelect(row.menu_code, row.name)}}>
                        Select
                      </Button>
                    </TableCell>
                  </TableRow>)
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </div>
      </DialogContent>
      <DialogActions>
        <Button onClick={onDialogClose} color="primary">
          Cancel
        </Button>
        <Button onClick={onAddClicked} color="primary" disabled={getDisabledStatus()}>
          ADD
        </Button>
      </DialogActions>
    </Dialog>
  );
}

AddOrderDialog.propTypes = {
  open: PropTypes.bool.isRequired,
  handleClose: PropTypes.func.isRequired,
  onItemSelected: PropTypes.func,
}