import React, {useEffect} from 'react';
import PropTypes  from 'prop-types';
import SingleOrderComponent from "./SingleOrderComponent";
import {updateOrderStatus} from "../../api/LambdaApi";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import {makeStyles} from "@material-ui/core/styles";
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import {Constants} from "../../utils/Constants";
import Typography from "@material-ui/core/Typography";
import {useRootContext} from "../../libs/ContextLibs";
import Button from "@material-ui/core/Button";
import RefreshIcon from '@material-ui/icons/Refresh';

const useStyles = makeStyles({
  root: {
    marginBottom: 16,
  },
  control: {
    minWidth: 275,
    margin: 16,
  },
  itemCount: {
    border: '1px solid lightgrey',
    padding: 16,
    alignItems: "center",
    display: 'inline-flex',
    verticalAlign: "center",
    marginLeft: 16,
  },
  checkBoxBorder: {
    border: '1px solid lightgrey',
    padding: 16,
    alignItems: "center",
  },
  refreshButton: {
    padding: 16
  }

});


export default function OrderQueueManagement(props) {
  const classes = useStyles();
  const rootContext = useRootContext();
  const [numberOfOrderShown, setNumberOfOrderShown] = React.useState(0);
  const [checkBoxState, setCheckBoxState] = React.useState({
    delivered: false,
    cancelled: false,
    inProgress: true,
  });


  const handleCheckBoxChange = (event) => {
    setCheckBoxState({
      ...checkBoxState,
      [event.target.name]: event.target.checked
    });
  }


  useEffect(() => {
    const interval = setInterval(() => {
      console.log('Clock ticking....');
      updateOrderQueue();
    }, 30000);

    return () => clearInterval(interval);
  }, []);


  useEffect(() => {
    if(props.orderQueue != null){
      let count = 0;
      Object.keys(props.orderQueue).forEach(orderId => {
        if(shouldDisplayOrder(props.orderQueue[orderId]) === true) count++;
      });
      setNumberOfOrderShown(count);
    }
  }, [props.orderQueue, checkBoxState]);


  const shouldDisplayOrder = (order) => {
    if(Constants.ORDER_PROCESS_STATUS.delivered.localeCompare(order.process_status) === 0){
      return checkBoxState.delivered;
    }
    if(Constants.ORDER_PROCESS_STATUS.cancelled.localeCompare(order.process_status) === 0){
      return checkBoxState.cancelled;
    }
    if(checkBoxState.inProgress === false){
      return false;
    }
    return true;
  }


  const updateOrderQueue = () => {
    props.getLastTwoHourOrder();
  }


  const updateStatus = async (order, newStatus) => {
    const params = {
      body: {
        order_timestamp_local: order.order_timestamp_local,
        update_order_timestamp_local: Date.now(),
        process_status: newStatus,
        parent_user_id: rootContext.userInfo.parent_user_id,
      }
    };
    try {
      await updateOrderStatus(params);
      updateOrderQueue();
    } catch (e) {
      console.log('Update Status: ' , e);
      alert('Couldn\'t update Status! Please try again!');
    }
  }


  return (
    <div className={classes.root}>
      <div className={classes.control}>
          <FormGroup row>
            <span className={classes.checkBoxBorder}>
              <FormControlLabel
                control={<Checkbox checked={checkBoxState.inProgress} color={"primary"}
                                   onChange={handleCheckBoxChange} name="inProgress" />}
                label="In Progress"
              />
              <FormControlLabel
                control={<Checkbox checked={checkBoxState.delivered} color={"primary"}
                                   onChange={handleCheckBoxChange} name="delivered" />}
                label="Delivered"
              />
              <FormControlLabel
                control={<Checkbox checked={checkBoxState.cancelled} color={"primary"}
                                   onChange={handleCheckBoxChange} name="cancelled" />}
                label="Cancelled"
              />
            </span>
            <span className={classes.itemCount}>
                <Typography>
                  Number of orders displayed: {numberOfOrderShown}
                </Typography>
            </span>
            <span className={classes.refreshButton}>
              <Button
                variant="outlined"
                color="primary"
                startIcon={<RefreshIcon />}
                onClick={props.refreshOrderQueue}
              >
                Refresh
              </Button>
            </span>
          </FormGroup>
      </div>
      <div>
        {Object.keys(props.orderQueue).sort().reverse().map((orderId, index) => {
          return (
            (shouldDisplayOrder(props.orderQueue[orderId])) &&
            (
              <SingleOrderComponent
                order={props.orderQueue[orderId]}
                updateStatus={updateStatus}
                serial={index + 1}
              />
            )
          )
        })}
        {
          (numberOfOrderShown === 0) &&
          (<Card elevation={3} variant={"elevation"} style={{margin: 16}}>
            <CardContent>
              No Orders To Display
            </CardContent>
          </Card>)
        }
      </div>
    </div>
  );
}

OrderQueueManagement.propTypes = {
  orderQueue: PropTypes.any.isRequired,
  getLastTwoHourOrder: PropTypes.func.isRequired,
  refreshOrderQueue: PropTypes.func.isRequired,
}


// sample order item
