// customer_contact: "01722286712"
// customer_name: "Muhim"
// delivery_address: "Khandar"
// delivery_partner: "Food Panda"
// menu_code_list: (2) ["02", "03"]
// order_id: "ap-southeast-1:01d47599-ddc3-487c-a3c2-a74e1d0ded25"
// order_timestamp_local: 1589411762035
// process_status: "NOT STARTED"
// quantity_list: (2) [2, 3]
// total_price: 1760
// ttl: 1590016562397