import { API } from "aws-amplify";
import { Constants } from "../utils/Constants";

export const createMenuApi = (params) => {
  return API.post(Constants.LINKS.API_NAME, Constants.ENDPOINTS.createMenuApi, params);
};

export const getMenuApi = (params) => {
  return API.post(Constants.LINKS.API_NAME, Constants.ENDPOINTS.getMenuApi, params);
};

export const deleteMenuApi = (params) => {
  return API.post(Constants.LINKS.API_NAME, Constants.ENDPOINTS.deleteMenuApi, params);
};

export const placeOrderApi = (params) => {
  return API.post(Constants.LINKS.API_NAME, Constants.ENDPOINTS.placeOrderApi, params);
};

export const getOrderQueueByTimeSegment = (params) => {
  return API.post(Constants.LINKS.API_NAME, Constants.ENDPOINTS.getOrderQueue, params);
};

export const updateOrderStatus = (params) => {
  return API.post(Constants.LINKS.API_NAME, Constants.ENDPOINTS.updateOrderStatus, params);
};

export const getUserInfo = () => {
  return API.get(Constants.LINKS.API_NAME, Constants.ENDPOINTS.getUserInfo);
};
